.component-container{
    height:90px;
    background-color: inherit;
}

.component{
    list-style-type: none;
    padding-left:0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.label{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    font-family: "Source Sans Pro", sans-serif;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 1rem;
    cursor: pointer;
}

.label > i {
    margin-left: 10px;
    transition: 0.9s ease-in-out text-decoration;
}

.label > div:hover{
    text-decoration: underline;
}